import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";

import { mapState } from "vuex";

export default {
    data() {
        return {
            theme: {
                color: '#ff4544'
            },
            tabList: [
                {id:1, name: '未完成'},
                {id:2, name: '已完成'}
            ],
            loading: null,
            list: [],
            activeTab: 1,
            page: 2,
            keyword: '',
            toSearch: false,
            haveKeyword: false,
        }
    },
    components: {
        "app-tab-nav": appTabNav,
    },
    computed: {
        ...mapState({
            mall: state => state.mallConfig.mall,
        })
    },
    methods: {
        open(e) {
            this.id = e;
        },

        goSearch() {
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.getList();
        },
        
        tabStatus(e) {
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.list = [];
            this.page = 2;
            this.activeTab = e.currentTarget.dataset.id;
            this.getList();
        },
        getSetting() {
            let that = this;
            that.$request({
                url: that.$api.bonus.setting,
            }).then(response=>{
                if(response.code == 0) {
                    that.setting = response.data.list;
                    if (that.setting.form && that.setting.form.orders) {
                        uni.setNavigationBarTitle({
                            title: that.setting.form.orders,
                        })
                    } else {
                        uni.setNavigationBarTitle({
                            title: '分红订单',
                        })
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$event.on(that.$const.EVENT_USER_LOGIN).then(()=>{
                    that.getSetting();
                });
            });
        },
        getList() {
            let that = this;
            that.$request({
                url: that.$api.bonus.order,
                data: {
                    status: that.activeTab,
                    keyword: that.keyword
                },
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
                that.$event.on(that.$const.EVENT_USER_LOGIN).then(()=>{
                    that.getList();
                });
            });
        },
        getMore() {
            let that = this;
            let url;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.$request({
                url: that.$api.bonus.order,
                data: {
                    status: that.activeTab,
                    keyword: that.keyword,
                    page: that.page
                },
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    if(response.data.list.length > 0) {
                        that.loading = null;
                        that.list = that.list.concat(response.data.list);
                        that.page++;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(e => {
                uni.hideLoading();
            });
        },
        cancelSeacrch() {
            this.keyword = '';
            this.toSearch = !this.toSearch;
            this.getList();
            this.$hideLoading();
        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        if (options.nickname) {
            that.keyword = options.nickname;
            that.haveKeyword = true;
            that.toSearch = true;
        }
        that.$showLoading({
            text: '加载中...'
        });
        that.getSetting();
        that.getList();
    },
    onReachBottom() {
        this.getMore();
    }
}
